@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@600&display=swap);
body {
  font-family: 'Open Sans', sans-serif;
  font-size: 24x;
  color:#3a3a3a;
  
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* NAVBAR */

nav.navbar { 
  display: flex;
  padding: 20px;
  background: #686de0; 
}

nav.navbar .navbar-user{
  margin-left: 10px;
  color: white;
}

button.navbar-button {
  padding: 5px 20px;
  width: 100px;
  border-radius: 3px;
  margin: 0 10px;
}

#home-btn {
  color: white;
  text-decoration: none;
}

/* GENERAL COMPONENTS */
.whiteBoardTitle {
  text-transform: lowercase;
  color: #ec6ba7;
  font-family: 'Spartan', sans-serif;
  margin-top: 50px;
  font-size: 50px;
  letter-spacing: 2px;
}
/* LOGIN-REGISTER PAGE */

#idSignupAndLogin {
  display:flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

#idSignupAndLogin .signupLoginLinks {
  margin-top: 30px;
  display:flex;
}

#idSignupAndLogin button {
  border: none;
  height: 50px;
  width: 150px;
  font-size: 20px;
  /* color:#3a3a3a; */
  color: #000;
  cursor: pointer;
  margin: 0 10px 0 0;
}

#idSignupAndLogin .activeButton {
  /* background-color: rgb(215, 215, 215); */
  background-color: #39a29d;
  color: #fff;
  
}

#idSignupAndLogin form {
  display:flex;
  flex-direction: column;
  align-items: left;
  text-align: center;
  margin: 40px 0 0 0;
  padding: 30px 30px 50px 30px;
  border-radius: 10px;
  box-shadow: -1px 5px 28px 0px rgba(0,0,0,0.15);
}
#idSignupAndLogin label{
margin: 15px 0 10px 0;
text-align: left;
}

#idSignupAndLogin input{
  width: 300px;
  height: 40px;
  margin: 5px 0 0 0;
  text-align: center;
  font-size: 1rem;

}

#idSignupAndLogin input:focus {
  outline: none;
  border:1px solid #39a29d;
  box-shadow: 0 0 3px #39a29d;
}

#idSignupAndLogin .submit{
 height: 45px;
 margin: 30px 0 0 0;
 color: #fff;
 border: none;
 background-color: #39a29d;
 border-radius: 5px;
 cursor: pointer;
}

#idSignupAndLogin p{
margin: 40px 0 0 0;
}
