#profile{
  display:flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

#profile img{
  width: 120px;
  height: 100px;
}

#profile button{
  margin-top: 25px;
    border: none;
  height: 50px;
  width: 150px;
  font-size: 20px;
